p><p>Sample Content.</p><p>Sample Content.</p><p><input type=&quot;text&quot; value=&quot;&quot; /></p><p>Sample Content.</p></div>' );\n//              }, 1000);\n//          }\n//      )})();void(0);\">Replace modal body.</a></li>\n// </ul>\n//\n// Styleguide 7.1\n","// Notice\n//\n// お知らせ。\n//\n// Styleguide 5.0\n\n// Notice\n//\n// 各種のお知らせ欄を表示します。\n//\n// Markup:\n// <div class=\"px2-notice\"><p>Notice</p></div>\n// <div class=\"px2-notice px2-notice--success\"><p>Success</p></div>\n// <div class=\"px2-notice px2-notice--warning\"><p>Warning</p></div>\n// <div class=\"px2-notice px2-notice--danger\"><p>Danger</p></div>\n// <div class=\"px2-notice px2-notice--info\"><p>Info</p></div>\n//\n// Styleguide 5.1\n.px2-notice {\n\tmargin-top: 1.5em;\n\tmargin-bottom: 1.5em;\n\tpadding: 0.75em 1.25em;\n\tborder-radius: 0.25em;\n\n\tcolor: #004085;\n\tbackground-color: #cce5ff;\n\tborder: 1px solid #b8daff;\n\n\t*{\n\t\tmargin-top: 0;\n\t\tmargin-bottom: 0;\n\t}\n}\n.px2-notice.px2-notice--success{\n\tcolor: #155724;\n\tbackground-color: #d4edda;\n\tborder-color: #c3e6cb;\n}\n.px2-notice.px2-notice--warning{\n\tcolor: #856404;\n\tbackground-color: #fff3cd;\n\tborder-color: #ffeeba;\n}\n.px2-notice.px2-notice--danger{\n\tcolor: #721c24;\n\tbackground-color: #f8d7da;\n\tborder-color: #f5c6cb;\n}\n.px2-notice.px2-notice--info{\n\tcolor: #0c5460;\n\tbackground-color: #d1ecf1;\n\tborder-color: #bee5eb;\n}\n\n// Flash Message\n//\n// Show Flash Message from JavaScript.\n//\n// Markup:\n// <ul>\n//  <li><a href=\"javascript:px2style.flashMessage(\n//          'Flash Message',\n//          function(){\n//              console.log('done.');\n//          }\n//      );\">Show Flash Message.</a></li>\n//  <li><a href=\"javascript:px2style.flashMessage(\n//          {\n//              'message':'Flash Message',\n//              'type':'success',\n//          },\n//          function(){\n//              console.log('done.');\n//          }\n//      );\">Show Flash Message -- success.</a></li>\n//  <li><a href=\"javascript:px2style.flashMessage(\n//          {\n//              'message':'Flash Message',\n//              'type':'warning',\n//          },\n//          function(){\n//              console.log('done.');\n//          }\n//      );\">Show Flash Message -- warning.</a></li>\n//  <li><a href=\"javascript:px2style.flashMessage(\n//          {\n//              'message':'Flash Message',\n//              'type':'danger',\n//          },\n//          function(){\n//              console.log('done.');\n//          }\n//      );\">Show Flash Message -- danger.</a></li>\n//  <li><a href=\"javascript:px2style.flashMessage(\n//          {\n//              'message':'Flash Message',\n//              'type':'info',\n//          },\n//          function(){\n//              console.log('done.');\n//          }\n//      );\">Show Flash Message -- info.</a></li>\n// </ul>\n//\n// Styleguide 5.2\n","// Utilities\n//\n// 各種ユーティリティ。\n//\n// Styleguide 9.0\n\n// Editor Type Badge\n//\n// 編集モードを表示します。\n//\n// Markup:\n// <span class=\"px2-editor-type px2-editor-type--not-exists\"></span>\n// <span class=\"px2-editor-type px2-editor-type--page-not-exists\"></span>\n// <span class=\"px2-editor-type px2-editor-type--alias\"></span>\n// <span class=\"px2-editor-type px2-editor-type--html\"></span>\n// <span class=\"px2-editor-type px2-editor-type--html-gui\"></span>\n// <span class=\"px2-editor-type px2-editor-type--txt\"></span>\n// <span class=\"px2-editor-type px2-editor-type--md\"></span>\n// <span class=\"px2-editor-type px2-editor-type--jade\"></span>\n// <p><code>.px2-editor-type--fullwidth</code> を付加して最大幅にできます。</p>\n// <span class=\"px2-editor-type px2-editor-type--html-gui px2-editor-type--fullwidth\"></span>\n//\n// Styleguide 9.1\n@mixin px2-editor-type{\n\tdisplay: inline-block;\n\tfont-size: 12px;\n\tfont-family: sans-serif;\n\tline-height: 1;\n\tpadding: 0.4em 1em;\n\ttext-align: center;\n\tcolor: #fff;\n}\n.px2-editor-type{\n\t@include px2-editor-type;\n\n\t&__html, // <- deprecated (古い書き方ですが、後方互換のために残します)\n\t&--html{\n\t\t@include px2-editor-type;\n\t\tbackground-color: #4a8a98;\n\t\t&:before{\n\t\t\tcontent: \"HTML\";\n\t\t}\n\t}\n\t&__html-gui, // <- deprecated (古い書き方ですが、後方互換のために残します)\n\t&--html-gui{\n\t\t@include px2-editor-type;\n\t\tbackground-color: #9f5b75;\n\t\t&:before{\n\t\t\tcontent: \"Block\"; // 以前は GUI と表記していました。2021-08-01 改称\n\t\t}\n\t}\n\t&__txt, // <- deprecated (古い書き方ですが、後方互換のために残します)\n\t&--txt{\n\t\t@include px2-editor-type;\n\t\tbackground-color: #999;\n\t\t&:before{\n\t\t\tcontent: \"TEXT\";\n\t\t}\n\t}\n\t&__md, // <- deprecated (古い書き方ですが、後方互換のために残します)\n\t&--md{\n\t\t@include px2-editor-type;\n\t\tbackground-color: #ae9b59;\n\t\t&:before{\n\t\t\tcontent: \"Markdown\";\n\t\t}\n\t}\n\t&__jade, // <- deprecated (古い書き方ですが、後方互換のために残します)\n\t&--jade{\n\t\t@include px2-editor-type;\n\t\tbackground-color: #999;\n\t\t&:before{\n\t\t\tcontent: \"Jade\";\n\t\t}\n\t}\n\t&__not-exists, // <- deprecated (古い書き方ですが、後方互換のために残します)\n\t&--not-exists{\n\t\t@include px2-editor-type;\n\t\tbackground-color: #c3c3c3;\n\t\t&:before{\n\t\t\tcontent: \"未作成\";\n\t\t}\n\t}\n\t&__page-not-exists, // <- deprecated (古い書き方ですが、後方互換のために残します)\n\t&--page-not-exists{\n\t\t@include px2-editor-type;\n\t\tbackground-color: #c3c3c3;\n\t\t&:before{\n\t\t\tcontent: \"ページ未作成\";\n\t\t}\n\t}\n\t&__alias, // <- deprecated (古い書き方ですが、後方互換のために残します)\n\t&--alias{\n\t\t@include px2-editor-type;\n\t\tbackground-color: #c3c3c3;\n\t\t&:before{\n\t\t\tcontent: \"ALIAS\";\n\t\t}\n\t}\n\t&--fullwidth{\n\t\twidth: auto;\n\t\tdisplay: block;\n\t\tfloat:none;\n\t}\n}\n\n// Loading\n// \n// Thanks: https://github.com/ConnorAtherton/loaders.css\n//\n// Markup:\n// <div class=\"px2-loading\"></div>\n// \n// <a href=\"javascript:loadingDEMO();\">loadingDEMO</a>\n// <script>\n// function loadingDEMO(){\n// \tpx2style.loading(); // Display Loading animation.\n// \tpx2style.loadingMessage('Loading Message 1...'); // Update Loading message.\n// \tsetTimeout