ist Item 3\n// \t\t\t<ul>\n// \t\t\t\t<li>List Item 1</li>\n// \t\t\t\t<li>List Item 2\n// \t\t\t\t</li>\n// \t\t\t\t<li>List Item 3</li>\n// \t\t\t</ul>\n// \t\t</li>\n// \t</ol>\n// \t<div class=\"px2-p\">\n// \t\t<table class=\"px2-table\">\n// \t\t\t<tbody>\n// \t\t\t\t<tr>\n// \t\t\t\t\t<th>th</th>\n// \t\t\t\t\t<td>td</td>\n// \t\t\t\t</tr>\n// \t\t\t</tbody>\n// \t\t</table>\n// \t</div>\n// \t<p class=\"px2-p\">Slim Paragraph.</p>\n// </div>\n//\n// Styleguide 9.3\n.px2-document{\n\tfont-size: medium;\n\tline-height: 1.5;\n\n\t// paragraph\n\tp{\n\t\tmargin-top: 1em;\n\t\tmargin-bottom: 1em;\n\t}\n\n\t// headers\n\th1{\n\t\tfont-weight:bold;\n\t\tmargin-top: 2em;\n\t\tmargin-bottom: 0.5em;\n\t}\n\th2, h3, h4, h5, h6{\n\t\tfont-weight:bold;\n\t\tmargin-top: 1.5em;\n\t\tmargin-bottom: 0.5em;\n\t}\n\th1{font-size: 240%;}\n\th2{font-size: 180%;}\n\th3{font-size: 160%;}\n\th4{font-size: 130%;}\n\th5{font-size: 120%;}\n\th6{font-size: 110%;}\n\n\t// lists\n\tul, ol {\n\t\tdisplay: block;\n\t\tmargin-top: 1em;\n\t\tmargin-bottom: 1em;\n\t\tpadding-left: 40px;\n\t\tul, ol {\n\t\t\tmargin-top: 0;\n\t\t\tmargin-bottom: 0;\n\t\t}\n\t}\n}\n\n// Paragraph\n//\n// Markup:\n// <div class=\"px2-p\">\n// \t前後に、p要素と同等のマージンを付加します。\n// </div>\n//\n// Styleguide 9.4\n.px2-p{\n\tmargin-top: 1em;\n\tmargin-bottom: 1em;\n}\n\n// 文字組み\n// \n// Markup:\n// <div class=\"px2-text-align-left\">\n// \t左寄せ\n// </div>\n// <div class=\"px2-text-align-center\">\n// \t中央寄せ\n// </div>\n// <div class=\"px2-text-align-right\">\n// \t右寄せ\n// </div>\n//\n// Styleguide 9.5\n.px2-text-align-left{\n\ttext-align: left;\n}\n.px2-text-align-center{\n\ttext-align: center;\n}\n.px2-text-align-right{\n\ttext-align: right;\n}\n\n// 文字サイズ\n// \n// Markup:\n// <div class=\"px2-font-size-ss\">\n// \t最も小さい文字サイズ\n// </div>\n// <div class=\"px2-font-size-s\">\n// \t小さい文字サイズ\n// </div>\n// <div class=\"px2-font-size-l\">\n// \t大きい文字サイズ\n// </div>\n// <div class=\"px2-font-size-ll\">\n// \t最も大きい文字サイズ\n// </div>\n//\n// Styleguide 9.6\n.px2-font-size-ss{\n\tfont-size: 74%;\n}\n.px2-font-size-s{\n\tfont-size: 88%;\n}\n.px2-font-size-l{\n\tfont-size: 112%;\n}\n.px2-font-size-ll{\n\tfont-size: 136%;\n}\n\n// Slimize\n//\n// Markup:\n// <h1 class=\"px2-slim\">Header 1</h1>\n// <h2 class=\"px2-slim\">Header 2</h2>\n// <h3 class=\"px2-slim\">Header 3</h3>\n// <h4 class=\"px2-slim\">Header 4</h4>\n// <h5 class=\"px2-slim\">Header 5</h5>\n// <h6 class=\"px2-slim\">Header 6</h6>\n// <p class=\"px2-slim\">\n// \tmargin と padding を 0 にします。\n// </p>\n// <ul class=\"px2-slim\">\n// \t<li>margin と padding を 0 にします。</li>\n// </ul>\n// <ol class=\"px2-slim\">\n// \t<li>margin と padding を 0 にします。</li>\n// </ol>\n// <div class=\"px2-slim px2-slim--r\">\n// \t<p>子要素も含めて、margin と padding を再帰的に 0 にします。</p>\n// \t<ul>\n// \t\t<li>子要素も含めて、margin と padding を再帰的に 0 にします。</li>\n// \t</ul>\n// </div>\n//\n// Styleguide 9.7\n.px2-slim{\n\tmargin: 0;\n\tpadding: 0;\n\n\t&--r *{\n\t\tmargin: 0;\n\t\tpadding: 0;\n\t}\n}\n\n// Responsive Block\n//\n// Markup:\n// <div class=\"px2-responsive\">\n// \t<table class=\"px2-table\">\n// \t\t<tbody>\n// \t\t\t<tr>\n// \t\t\t\t<th>th</th>\n// \t\t\t\t<td>1000000000</td>\n// \t\t\t\t<td>2000000000</td>\n// \t\t\t\t<td>3000000000</td>\n// \t\t\t\t<td>4000000000</td>\n// \t\t\t\t<td>5000000000</td>\n// \t\t\t\t<td>6000000000</td>\n// \t\t\t\t<td>7000000000</td>\n// \t\t\t\t<td>8000000000</td>\n// \t\t\t\t<td>9000000000</td>\n// \t\t\t\t<td>10000000000</td>\n// \t\t\t\t<td>11000000000</td>\n// \t\t\t\t<td>12000000000</td>\n// \t\t\t\t<td>13000000000</td>\n// \t\t\t</tr>\n// \t\t</tbody>\n// \t</table>\n// </div>\n//\n// Styleguide 9.8\n.px2-responsive{\n\toverflow: auto;\n}\n","// Tables\n//\n// Markup:\n// <table class=\"px2-table\">\n//     <thead>\n//         <tr>\n//             <th>head1</th>\n//             <th>head2</th>\n//             <th>head3</th>\n//             <th>head4</th>\n//         </tr>\n//     </thead>\n//     <tfoot>\n//         <tr>\n//           